import React, { useState, useEffect } from 'react';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, LineChart, Line } from 'recharts';

const EmpireMetricsDashboard = () => {
  const [metrics, setMetrics] = useState({
    outreach: '',
    responses: '',
    calls: '',
    revenue: '',
    energy: 5,
    notes: ''
  });

  const [dailyData, setDailyData] = useState([]);
  const [currentDate, setCurrentDate] = useState(new Date().toISOString().split('T')[0]);

  useEffect(() => {
    const savedData = localStorage.getItem('empireMetrics');
    if (savedData) {
      setDailyData(JSON.parse(savedData));
    }
  }, []);

  const calculateROI = (outreach, responses, calls, revenue, energy) => {
    if (!outreach || outreach === 0) return 0;
    const score = ((parseInt(responses || 0) * 2) + (parseInt(calls || 0) * 5) + (parseInt(revenue || 0) * 10)) / parseInt(outreach) * (parseInt(energy) / 10);
    return Math.round(score * 10) / 10;
  };

  const handleSubmit = () => {
    const newEntry = {
      date: currentDate,
      outreach: parseInt(metrics.outreach || 0),
      responses: parseInt(metrics.responses || 0),
      calls: parseInt(metrics.calls || 0),
      revenue: parseInt(metrics.revenue || 0),
      energy: parseInt(metrics.energy),
      notes: metrics.notes,
      roi: calculateROI(metrics.outreach, metrics.responses, metrics.calls, metrics.revenue, metrics.energy)
    };

    const updatedData = [...dailyData.filter(item => item.date !== currentDate), newEntry]
      .sort((a, b) => new Date(a.date) - new Date(b.date))
      .slice(-30);

    setDailyData(updatedData);
    localStorage.setItem('empireMetrics', JSON.stringify(updatedData));
    
    setMetrics({
      outreach: '',
      responses: '',
      calls: '',
      revenue: '',
      energy: 5,
      notes: ''
    });

    alert('Metrics saved successfully!');
  };

  const handleInputChange = (field, value) => {
    setMetrics(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const currentROI = calculateROI(metrics.outreach, metrics.responses, metrics.calls, metrics.revenue, metrics.energy);
  
  const getROIColor = (roi) => {
    if (roi >= 7) return 'text-green-500';
    if (roi >= 4) return 'text-yellow-500';
    return 'text-red-500';
  };

  const getROIBadge = (roi) => {
    if (roi >= 7) return 'bg-green-500';
    if (roi >= 4) return 'bg-yellow-500';
    return 'bg-red-500';
  };

  const todaysData = dailyData.find(item => item.date === currentDate);
  const weekData = dailyData.slice(-7);
  const avgROI = weekData.length > 0 ? weekData.reduce((sum, item) => sum + item.roi, 0) / weekData.length : 0;

  const exportData = () => {
    const dataStr = JSON.stringify(dailyData, null, 2);
    const dataBlob = new Blob([dataStr], {type: 'application/json'});
    const url = URL.createObjectURL(dataBlob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'empire-metrics-backup.json';
    link.click();
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-blue-900 to-black text-white p-6">
      <div className="max-w-6xl mx-auto">
        
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold bg-gradient-to-r from-blue-400 to-purple-500 bg-clip-text text-transparent mb-2">
            EMPIRE METRICS OS
          </h1>
          <p className="text-gray-300">Tesla-grade execution intelligence</p>
        </div>

        <div className="text-center mb-8">
          <div className="inline-block">
            <div className={`text-6xl font-bold ${getROIColor(currentROI)} mb-2`}>
              {currentROI.toFixed(1)}
            </div>
            <div className={`inline-block px-4 py-1 rounded-full text-white text-sm ${getROIBadge(currentROI)}`}>
              LIVE ROI SCORE
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          
          <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
            <h2 className="text-2xl font-bold mb-6 text-blue-400">Daily Input</h2>
            
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium mb-2">Date</label>
                <input
                  type="date"
                  value={currentDate}
                  onChange={(e) => setCurrentDate(e.target.value)}
                  className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-md text-white"
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium mb-2">Outreach Sent</label>
                  <input
                    type="number"
                    value={metrics.outreach}
                    onChange={(e) => handleInputChange('outreach', e.target.value)}
                    className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-md text-white"
                    placeholder="10"
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium mb-2">Responses</label>
                  <input
                    type="number"
                    value={metrics.responses}
                    onChange={(e) => handleInputChange('responses', e.target.value)}
                    className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-md text-white"
                    placeholder="2"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium mb-2">Calls Booked</label>
                  <input
                    type="number"
                    value={metrics.calls}
                    onChange={(e) => handleInputChange('calls', e.target.value)}
                    className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-md text-white"
                    placeholder="1"
                  />
                </div>
                
                <div>
                  <label className="block text-sm font-medium mb-2">Revenue Events</label>
                  <input
                    type="number"
                    value={metrics.revenue}
                    onChange={(e) => handleInputChange('revenue', e.target.value)}
                    className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-md text-white"
                    placeholder="0"
                  />
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium mb-2">Energy Level: {metrics.energy}/10</label>
                <input
                  type="range"
                  min="1"
                  max="10"
                  value={metrics.energy}
                  onChange={(e) => handleInputChange('energy', e.target.value)}
                  className="w-full h-2 bg-gray-700 rounded-lg appearance-none cursor-pointer"
                />
              </div>

              <div>
                <label className="block text-sm font-medium mb-2">Notes</label>
                <textarea
                  value={metrics.notes}
                  onChange={(e) => handleInputChange('notes', e.target.value)}
                  className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-md text-white"
                  rows="3"
                  placeholder="Key insights, patterns, or wins..."
                />
              </div>

              <button
                onClick={handleSubmit}
                className="w-full bg-gradient-to-r from-blue-600 to-purple-600 text-white py-3 px-4 rounded-md font-medium hover:from-blue-700 hover:to-purple-700 transition-all"
              >
                Save Metrics
              </button>
            </div>
          </div>

          <div className="space-y-6">
            
            <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
              <h3 className="text-xl font-bold mb-4 text-blue-400">Today's Status</h3>
              {todaysData ? (
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>Outreach: <span className="font-bold text-blue-400">{todaysData.outreach}</span></div>
                  <div>Responses: <span className="font-bold text-green-400">{todaysData.responses}</span></div>
                  <div>Calls: <span className="font-bold text-purple-400">{todaysData.calls}</span></div>
                  <div>Revenue: <span className="font-bold text-yellow-400">{todaysData.revenue}</span></div>
                  <div className="col-span-2">Energy: <span className="font-bold">{todaysData.energy}/10</span></div>
                  <div className="col-span-2">ROI: <span className={`font-bold ${getROIColor(todaysData.roi)}`}>{todaysData.roi}</span></div>
                </div>
              ) : (
                <p className="text-gray-400">No data for today yet</p>
              )}
            </div>

            <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
              <h3 className="text-xl font-bold mb-4 text-blue-400">This Week</h3>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span>Avg ROI:</span>
                  <span className={`font-bold ${getROIColor(avgROI)}`}>{avgROI.toFixed(1)}</span>
                </div>
                <div className="flex justify-between">
                  <span>Days Tracked:</span>
                  <span className="font-bold">{weekData.length}/7</span>
                </div>
                <div className="flex justify-between">
                  <span>Total Outreach:</span>
                  <span className="font-bold text-blue-400">{weekData.reduce((sum, item) => sum + item.outreach, 0)}</span>
                </div>
                <div className="flex justify-between">
                  <span>Total Responses:</span>
                  <span className="font-bold text-green-400">{weekData.reduce((sum, item) => sum + item.responses, 0)}</span>
                </div>
              </div>
            </div>

            <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
              <h3 className="text-xl font-bold mb-4 text-blue-400">ROI Trend</h3>
              {dailyData.length > 0 ? (
                <ResponsiveContainer width="100%" height={200}>
                  <LineChart data={dailyData.slice(-14)}>
                    <CartesianGrid strokeDasharray="3 3" stroke="#374151" />
                    <XAxis 
                      dataKey="date" 
                      stroke="#9CA3AF"
                      fontSize={12}
                      tickFormatter={(date) => new Date(date).getMonth() + 1 + '/' + new Date(date).getDate()}
                    />
                    <YAxis stroke="#9CA3AF" fontSize={12} />
                    <Tooltip 
                      contentStyle={{ backgroundColor: '#1F2937', border: '1px solid #374151', borderRadius: '6px' }}
                      labelStyle={{ color: '#E5E7EB' }}
                    />
                    <Line 
                      type="monotone" 
                      dataKey="roi" 
                      stroke="#3B82F6" 
                      strokeWidth={2}
                      dot={{ fill: '#3B82F6', strokeWidth: 2, r: 4 }}
                    />
                  </LineChart>
                </ResponsiveContainer>
              ) : (
                <p className="text-gray-400 text-center py-8">Start tracking to see trends</p>
              )}
            </div>
          </div>
        </div>

        <div className="mt-8 text-center">
          <button
            onClick={exportData}
            className="bg-gray-700 hover:bg-gray-600 text-white py-2 px-4 rounded-md transition-colors"
          >
            Export Data
          </button>
        </div>
      </div>
    </div>
  );
};

export default EmpireMetricsDashboard;